@use "../utils/colors" as colors;

@mixin overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.5);
}

@mixin navbarLinkActiveAndHover {
  background-color: colors.$black;
  color: colors.$light;
}

@mixin buttonAndLinkStyles($bgColor: colors.$light, $txtColor: colors.$darkgray) {
  position: relative;
  display: inline-block;
  padding: .75em 4.5em;
  background-color: $bgColor;
  color: $txtColor;
  border: 2px solid $txtColor;
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
}

@mixin buttonAndLinkHover($hoverBgColor: colors.$lightgray) {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $hoverBgColor;
  z-index: -1;
  transition: left 300ms ease-out, right 300ms ease-out;  
}

@mixin formControlStyles {
  margin-bottom: .25em;
  width: 100%;
  padding: 1em;
  border: 2px solid transparent;
  font-size: 1.6rem;
  font-family: var(--DeliciousHandrawn-Regular);
}

@mixin companyLogo {
  display: flex;
  align-items: center;
  gap: .25em;
  font-size: 3.2rem;
  font-family: var(--DeliciousHandrawn-Regular);
  color: colors.$black;
  text-decoration: none;
}

@mixin iconHeight {
  display: inline-flex;
  height: 26px;
  vertical-align: middle;
}