  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "DeliciousHandrawn-Regular";
  src: url("../assets/fonts/AnyConv.com__DeliciousHandrawn-Regular.woff2") format("woff2"), url("../assets/fonts/DeliciousHandrawn-Regular.woff") format("woff"), url("../assets/fonts/DeliciousHandrawn-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
