@font-face {
  font-family: "Primary_italic";
  src: url("../assets/fonts/poppins-italic-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-italic-webfont.woff") format("woff"), url("../assets/fonts/poppins-italic-webfont.ttf") format("truetype");
