  src: url("../assets/fonts/poppins-light-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-light-webfont.woff") format("woff"), url("../assets/fonts/poppins-light-webfont.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_medium";
  src: url("../assets/fonts/poppins-medium-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-medium-webfont.woff") format("woff"), url("../assets/fonts/poppins-medium-webfont.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_semibold";
  src: url("../assets/fonts/poppins-semibold-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-semibold-webfont.woff") format("woff"), url("../assets/fonts/poppins-semibold-webfont.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_bold";
  src: url("../assets/fonts/poppins-bold-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-bold-webfont.woff") format("woff"), url("../assets/fonts/poppins-bold-webfont.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_black";
  src: url("../assets/fonts/poppins-black-webfont.woff2") format("woff2"), url("../assets/fonts/poppins-black-webfont.woff") format("woff"), url("../assets/fonts/poppins-black-webfont.ttf") format("truetype");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --DeliciousHandrawn-Regular:"DeliciousHandrawn-Regular,sans-serif";
  --primary-font: "Primary";
  --primary-font-italic: "Primary_italic", Arial, sans-serif;
  --primary-font-extralight: "Primary_extralight", Arial, sans-serif;
  --primary-font-light: "Primary_light", Arial, sans-serif;
  --primary-font-medium: "Primary_medium", Arial, sans-serif;
  --primary-font-semibold: "Primary_semibold", Arial, sans-serif;
  --primary-font-bold: "Primary_bold", Arial, sans-serif;
  --primary-font-black: "Primary_black", Arial, sans-serif;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

[id] {
  scroll-margin-top: 440px;
}

body {
  font-size: 1.6rem;
  font-family: var(--DeliciousHandrawn-Regular);
  background-color: hsl(0, 0%, 90%);
}

.container-fluid {
  width: 100%;
  position: sticky;
  top: 0;
  background-color: hsl(0, 0%, 90%);
  border-bottom: 2px solid black;
  z-index: 11;
}
.container-fluid--dark {
  background-color: hsl(0, 0%, 0%);
}

.navbar__overlay--active {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.5);
  z-index: 10;
}

.container {
  width: 94%;
  margin: 0 auto;
}

.section__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.5);
  z-index: 8;
}

.section__title,
.section__subtitle {
  text-align: center;
}

.section__title {
  margin-top: 3.2em;
  font-size: 2.8rem;
  font-family: var(--DeliciousHandrawn-Regular);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.section__title-divider {
  margin: 1.5em auto;
  width: 60px;
  border: 2px solid hsl(44, 71%, 51%);
}

.section__subtitle {
  font-size: 1.6rem;
  margin-bottom: 2.375em;
  line-height: 1.5;
}

.cookie-alert {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 2em;
  background-color: hsl(0, 0%, 90%);
  border-top: 2px solid hsl(0, 0%, 0%);
  z-index: 12;
  transform: translateY(100%);
  animation: showCookieAlert 1s 500ms forwards;
}
.cookie-alert__message, .cookie-alert__button {
  margin-top: 1.5em;
}
.cookie-alert__link {
  color: hsl(44, 71%, 51%);
}
.cookie-alert__button {
  position: relative;
  display: inline-block;
  padding: 0.75em 4.5em;
  background-color: hsl(44, 71%, 51%);
  color: hsl(0, 0%, 0%);
  border: 2px solid hsl(0, 0%, 0%);
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
  border: 2px solid transparent;
  cursor: pointer;
}

@keyframes showCookieAlert {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
.navbar {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0.5em;
  background-color: hsl(0, 0%, 90%);
}
.navbar__logo {
  margin-left: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.25em;
  font-size: 3.2rem;
  font-family: var(--DeliciousHandrawn-Regular);
  color: hsl(0, 0%, 0%);
  text-decoration: none;
}
.navbar__logo-icon {
  width: 25px;
}
.navbar__burger-icon {
  position: relative;
  width: 72px;
  height: 66px;
  padding: 1em;
  background: none;
  border: 2px solid transparent;
  border-radius: 4px;
  font-size: 1.6rem;
  cursor: pointer;
}
.navbar__burger-icon-line {
  display: block;
}
.navbar__burger-icon-line, .navbar__burger-icon-line::before, .navbar__burger-icon-line::after {
  width: 37px;
  height: 4px;
  background-color: hsl(0, 0%, 0%);
  border-radius: 2px;
}
.navbar__burger-icon-line::before, .navbar__burger-icon-line::after {
  content: "";
  position: absolute;
  left: 16px;
  transition: transform 200ms ease-in-out;
}
.navbar__burger-icon-line::before {
  top: 16px;
}
.navbar__burger-icon-line::after {
  bottom: 16px;
}
.navbar__burger-icon--active > .navbar__burger-icon-line::before {
  transform: translateY(-4px);
}
.navbar__burger-icon--active > .navbar__burger-icon-line::after {
  transform: translateY(4px);
}
.navbar__burger-icon:focus {
  outline: none;
  border-color: hsl(0, 0%, 0%);
}
.navbar__links {
  display: none;
  flex-basis: 100%;
}
.navbar__links--active {
  display: block;
}
.navbar__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
}
.navbar__item {
  width: 100%;
  text-align: center;
}
.navbar__link {
  width: 100%;
  display: inline-block;
  padding: 2em 1em;
  text-decoration: none;
  color: hsl(0, 0%, 0%);
  transition: background-color 100ms linear, color 200ms linear;
}
@media (hover: hover) {
  .navbar__link:hover {
    background-color: hsl(0, 0%, 0%);
    color: hsl(0, 0%, 90%);
  }
}
.navbar__link--active {
  background-color: hsl(0, 0%, 0%);
  color: hsl(0, 0%, 90%);
}

.hero {
  height: 25vh;
  position: relative;
}
.hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.hero__text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: hsl(0, 0%, 90%);
  z-index: 9;
}
.hero__title {
  margin-bottom: 0.5em;
  font-size: 1.4rem;
  font-family: var(--DeliciousHandrawn-Regular);
}
.hero__description {
  font-size: 1.8rem;
}

.about > .section__title-divider {
  margin-bottom: 3em;
}
.about__description {
  margin-bottom: 3em;
  display: flex;
  flex-direction: column;
  gap: 3em;
}
.about__image-box {
  width: 100%;
  object-fit: cover;
}
.about__image {
  width: 100%;
}
.about__text {
  color: hsl(36, 87%, 42%);
}
.about__title {
  margin-bottom: 1em;
  text-align: center;
  color: hsl(0, 0%, 0%);
  line-height: 1;
}
.about__mission {
  line-height: 1.8;
}
.about__stats {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 2em;
}
.about__stat {
  width: 210px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
}
.about__stat-icon-box {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: hsl(315, 100%, 75%);
}
.about__stat-icon {
  width: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.about__stat-number {
  font-family: var(--DeliciousHandrawn-Regular);
  font-size: 3.6rem;
}
.about__banner {
  margin-top: 3em;
  width: 100%;
  height: 20vh;
  position: relative;
  z-index: 0;
}
.about__banner-text-box {
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid hsl(0, 0%, 90%);
}
.about__banner-text-box > .about__banner-text {
  color: hsl(0, 0%, 90%);
  font-family: var(--DeliciousHandrawn-Regular);
  padding: 1em;
  text-align: center;
}
.about__banner > .section__overlay {
  z-index: -1;
}
.about__banner-image {
  background-image: url("../images/girl_walking_mobile.min.jpg");
  background-position: center center;
  background-size: cover;
}

.offer__card-group {
  margin-bottom: 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.offer__card {
  padding: 3em 2em;
  box-shadow: 0 2px 5px 0 hsl(36, 87%, 42%);
  border-radius: 4px;
  text-align: center;
  transform: scale(1);
  transition: transform 200ms ease-in-out;
}
.offer__card--popular {
  position: relative;
  border: 3px solid hsl(318, 56%, 57%);
  overflow: hidden;
}
.offer__card-label {
  width: 150px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.5em 1em;
  background-color: hsl(318, 56%, 57%);
  color: hsl(0, 0%, 90%);
  transform: rotate(-45deg) translate(-27%, -27%);
}
.offer__card-title {
  margin-bottom: 1em;
  font-family: var(--DeliciousHandrawn-Regular);
}
.offer__card-price {
  margin-bottom: 0.5em;
  font-family: var(--DeliciousHandrawn-Regular);
  font-size: 3.6rem;
  letter-spacing: -2px;
}
.offer__card-price-currency {
  font-size: 1.3em;
}
.offer__card-options-box {
  margin-bottom: 2em;
}
.offer__card-options-list {
  list-style-type: none;
}
.offer__card-option {
  margin-bottom: 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5em;
}
.offer__card-option-icon--available {
  fill: hsl(44, 71%, 51%);
  opacity: 1;
}
.offer__card-option-icon--unavailable {
  fill: hsl(36, 87%, 42%);
  opacity: 0.5;
}
.offer__card-option-icon--unavailable + span {
  opacity: 0.6;
}
.offer__card-link {
  position: relative;
  display: inline-block;
  padding: 0.75em 4.5em;
  background-color: hsl(0, 0%, 90%);
  color: hsl(36, 87%, 42%);
  border: 2px solid hsl(36, 87%, 42%);
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
}
.offer__card-link > span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsl(0, 0%, 83.8%);
  z-index: -1;
  transition: left 300ms ease-out, right 300ms ease-out;
}
.offer__card-link > span:first-child {
  top: -50%;
  left: -100%;
}
.offer__card-link > span:last-child {
  bottom: -50%;
  right: -100%;
}
.offer__card-link--popular {
  position: relative;
  display: inline-block;
  padding: 0.75em 4.5em;
  background-color: hsl(44, 71%, 51%);
  color: hsl(0, 0%, 0%);
  border: 2px solid hsl(0, 0%, 0%);
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
  border-color: hsl(318, 56%, 57%);
}
.offer__card-link--popular > span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsl(318, 56%, 57%);
  z-index: -1;
  transition: left 300ms ease-out, right 300ms ease-out;
}
@media (hover: hover) {
  .offer__card-link:hover > span:first-child {
    left: 0;
  }
  .offer__card-link:hover > span:last-child {
    right: 0;
  }
}
@media (hover: hover) {
  .offer__card:hover {
    transform: scale(1.05);
  }
}
.offer__card-option-icon {
  width: 1.6rem;
}
.offer__card-option-icon--available {
  fill: rgb(234, 186, 14);
}
.offer__link-box {
  text-align: center;
}
.offer__link {
  position: relative;
  display: inline-block;
  padding: 0.75em 4.5em;
  background-color: hsl(44, 71%, 51%);
  color: hsl(0, 0%, 0%);
  border: 2px solid hsl(0, 0%, 0%);
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
  border-color: hsl(318, 56%, 57%);
}
.offer__link > span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsl(318, 56%, 57%);
  z-index: -1;
  transition: left 300ms ease-out, right 300ms ease-out;
}
.offer__link > span:first-child {
  top: -50%;
  left: -100%;
}
.offer__link > span:last-child {
  bottom: -50%;
  right: -100%;
}
@media (hover: hover) {
  .offer__link:hover > span:first-child {
    left: 0;
  }
  .offer__link:hover > span:last-child {
    right: 0;
  }
}

.testimonials__slider {
  position: relative;
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.testimonials__slider-carousel {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 100%;
  gap: 1em;
  scroll-behavior: smooth;
  overflow: hidden;
}
.testimonials__slider-list {
  list-style-type: none;
}
.testimonials__slider-card {
  padding: 1.5em;
  border: 3px solid hsl(36, 87%, 42%);
  border-radius: 6px;
}
.testimonials__slider-card-top {
  margin-bottom: 1.5em;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.testimonials__slider-card-image {
  width: 100px;
  height: 100px;
  padding: 0.2em;
  object-fit: cover;
  border: 4px solid hsl(44, 71%, 51%);
  border-radius: 50%;
}
.testimonials__slider-card-quote {
  position: relative;
  width: 60px;
  height: 60px;
  background-color: hsl(44, 71%, 51%);
  border-radius: 50%;
}
.testimonials__slider-card-quote img {
  position: absolute;
  width: 70%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.testimonials__slider-card-text {
  margin-bottom: 1em;
  line-height: 1.5;
}
.testimonials__slider-card-fullname {
  font-size: 2.2rem;
  margin-bottom: 0.5em;
  color: hsl(44, 71%, 51%);
}
.testimonials__slider-card-city {
  color: hsl(36, 87%, 42%);
}
.testimonials__slider-button {
  position: relative;
  display: inline-block;
  padding: 0.75em 4.5em;
  background-color: hsl(44, 71%, 51%);
  color: hsl(36, 87%, 42%);
  border: 2px solid hsl(36, 87%, 42%);
  border-radius: 4px;
  font-family: var(--DeliciousHandrawn-Regular);
  text-decoration: none;
  overflow: hidden;
  z-index: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0.5em 0.4em 0.35em;
  font-size: 3rem;
  border: none;
  box-shadow: none;
  transition: none;
  cursor: pointer;
}
.testimonials__slider-button--prev {
  left: -20px;
}
.testimonials__slider-button--next {
  right: -20px;
}
.testimonials__companies {
  margin-bottom: 3em;
  display: flex;
  flex-direction: column;
}
.testimonials__company {
  margin: 2em 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonials__company-logo {
  width: 35%;
}
.testimonials__company:first-child > .testimonials__company-logo {
  width: 40%;
}

.contact__details, .contact__form {
  padding: 2em;
  border: 2px solid hsl(36, 87%, 42%);
  text-align: center;
}
.contact__details-box {
  margin-bottom: 1em;
}
.contact__details-address, .contact__details-phone, .contact__details-email {
  color: hsl(36, 87%, 42%);
  line-height: 1.4;
}
.contact__details-logo {
  display: flex;
  align-items: center;
  gap: 0.25em;
  font-size: 3.2rem;
  font-family: var(--DeliciousHandrawn-Regular);
  color: hsl(0, 0%, 0%);
  text-decoration: none;
  justify-content: center;
  padding-top: 1em;
  border-top: 2px solid hsl(36, 87%, 42%);
}
.contact__box {
  display: flex;
  flex-direction: column;
  gap: 2em;
}
.contact__title {
  margin-bottom: 1em;
  text-transform: uppercase;
}
.contact__subtitle {
  margin-bottom: 0.5em;
  font-size: 1.8rem;
}
.contact__form {
  position: relative;
}
.contact__form-title {
  text-transform: uppercase;
}
.contact__form-grid {
  display: grid;
  gap: 1em;
}
.contact__form-grid-item {
  margin-bottom: 1em;
}
.contact__form-grid-item:nth-last-of-type(2) > .contact__form-info-box {
  display: flex;
  justify-content: space-between;
}
.contact__form-grid-item:last-of-type {
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
}
.contact__form-label {
  display: block;
  margin-bottom: 0.5em;
  font-size: 1.8rem;
  font-family: var(--DeliciousHandrawn-Regular);
}
.contact__form-input {
  margin-bottom: 0.25em;
  width: 100%;
  padding: 1em;
  border: 2px solid transparent;
  font-size: 1.6rem;
  font-family: var(--DeliciousHandrawn-Regular);
}
.contact__form-input::-webkit-input-placeholder {
  color: hsl(36, 87%, 42%);
}
.contact__form-input:focus {
  outline: none;
  border-color: hsl(36, 87%, 42%);
}
.contact__form-input--select {
  margin-bottom: 0.25em;
  width: 100%;
  padding: 1em;
  border: 2px solid transparent;
  font-size: 1.6rem;
  font-family: var(--DeliciousHandrawn-Regular);
  appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
}
.contact__form-input--select:focus {
  outline: none;
  border: 2px solid hsl(36, 87%, 42%);
}
.contact__form-input-arrow {
  position: absolute;
  width: 20px;
  right: 0;
  margin: 1em;
  fill: hsl(36, 87%, 42%);
  opacity: 0.75;
  pointer-events: none;
}
.contact__form-input-arrow--up {
  top: 25px;
}
.contact__form-input-arrow--down {
  top: 27px;
}
.contact__form-input--textarea {
  min-height: 70px;
  resize: vertical;
}
.contact__form-input--error {
  border-color: hsl(0, 100%, 40%);
}
.contact__form-select-box {
  position: relative;
}
.contact__form-error {
  display: none;
  color: hsl(0, 100%, 40%);
  font-size: 1.6rem;
  text-align: left;
}
.contact__form-error--active {
  display: block;
}
.contact__form-counter {
