@media screen and (min-width: 768px) {
  .parks {
    &__grid {
      grid-template-columns: repeat(2, 1fr);
    }

    &__card {
      &-image {
        height: 250px;
      }

      &:last-child {
        width: 50%;
        grid-column: 1 / -1;
        justify-self: center;
      }
    }
  }
}